.map-wrapper {
  display: flex;
  justify-content: flex-start;
  align-items: left;
  width: 100%;
  height: 100vh;
  box-sizing: border-box;
  padding: 1%;
  background-color: transparent;
}

.map-container {
  position: relative;
  width: 100%;
  height: 100%;
  background-color: #969292;
  box-sizing: border-box;
  z-index: 1;
  background-color: rgba(30, 30, 30, 0.75);
}

.ol-layer {
  z-index: 10; /* Default OpenLayers z-index for map layers */
}

.ol-overlaycontainer-stopevent, .ol-overlaycontainer {
  z-index: 20; /* Ensures overlays and markers are higher than the base map */
}
.visibilityPanel {
  width: 20%;
  height: 100%;
  padding-left: 10px; /* Ensures the elements are close to the left */
  box-sizing: border-box;
  background-color: rgba(30, 30, 30, 0.9);
  margin-right: 3px;
  padding: 15px;
  overflow-y: scroll;
}
/* Add to your ControlPanel.css or main CSS file */
.drawing-button {
  margin: 10px 0;
  padding: 10px 20px;
  background-color: #6200ee;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.drawing-button:hover {
  background-color: #3700b3;
}

.category, .subcategory {
  margin-bottom: 10px;
}

.category-header, .subcategory-header, .item {
  display: flex;
  align-items: center;
}

input[type="checkbox"] {
  margin-right: 3px;
  margin-left: 3px;
  width:20%
}

label {
  cursor: pointer;
  margin-right: 5px;
}

.category-header {
  margin-bottom: 10px;
}

.subcategory-list {
  margin-left: 35px;
}

.subcategory-header {
  margin-bottom: 10px;
}

.item-list {
  margin-left: 45px; /* Aligns items under subcategories */
}

.item {
  margin-bottom: 10px;
}

.expand-arrow {
  cursor: pointer;
  margin-right: 0px;
  transition: transform 0.3s ease;
  display: inline-block;
  width: 10px;
}

.expand-arrow.expanded {
  transform: rotate(90deg); /* Rotates the arrow on expansion */
}
.custom-icon img {
  width: 50px; 
  height: 50px;
}
.ol-tooltip {
  background-color: rgba(30, 30, 30, 0.75);
  color: #fff;
  padding: 5px;
  border-radius: 3px;
  font-size: 16px;
  white-space: nowrap;
  position: relative;
  pointer-events: none;
  border: 1px solid white;
}

.category-dropdown-container {
  position: absolute;
  z-index: 999;
  width: auto; /* Allow the width to adjust */
  height: auto; /* Adjust the height dynamically */
}

.category-dropdown {
  background: rgb(88, 6, 147);
  color: white;
  border: 1px solid black;
  padding: 10px 20px;  /* Padding to make it consistent */
  z-index: 1000;
  width: 200px;  /* Set a wider width for the dropdown */
  font-size: 18px;
}

.category-dropdown select {
  width: 100%;
  font-size: 16px;
  color: rgba(255, 255, 255, 0.9);
  background: rgb(88, 6, 147);
  border: none;
  text-align: left;  /* Align text to the left */
  appearance: none;
}

.custom-icon {
  font-size: 22px;
}

.category-select-menu {
  width: 500px;
}

button {
  margin-top: 5px;
  padding: 8px 12px;
  font-size: 16px;
  cursor: pointer;
}
.icon-modal{
  width: 15%;
  margin: 3px;
  padding: 3px;
  background-color: rgba(30, 30, 30, 0.9);
  overflow-y: scroll;
  align-items: center;
}
.icon-modal-content{
  padding-bottom: 60px;
  align-items: center;
}
.icon-list-modal{
  list-style: none;
  padding:0;
}
.icon-image-modal{
  height: 30px;
  width: 30px;
}
.icon-label-modal{
  margin: -5px 0 30px 0;
}
.draggable-icon {
  pointer-events: auto;
  user-select: none;
}
.popup {
  background-color: rgba(30, 30, 30, 0.9);
  padding: 10px;
  border: 1px solid black;
  border-radius: 4px;
}

@media (max-width: 768px) { 
  .map-wrapper {
    display: flex;
    flex-direction: column;
    align-items: left;
    width: 100%;
    height: 100vh;
    box-sizing: border-box;
    padding: 1%;
    background-color: transparent;
  }
  .visibilityPanel {
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    position: relative;
    z-index: 50;
    background-color: rgba(30, 30, 30, 0.9);
    overflow-y:auto; 
    transform: none;
  }
  .visibilityPanel > .column-header { 
    flex: 1 0 30%;
    max-width: 30%; 
    box-sizing: border-box; 
    padding: 5px; 
  }
  input[type="checkbox"] {
    margin-right: 0px; /* Space between checkbox and label */
    margin-left: 5px;
    margin-right: 5px;
    width:15%
  }
  .map-container {
    margin-top: 10px; /* Adjust as needed to avoid overlap with sidebar */
  }
}


