body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

img {
  display: block;
  max-width: 100%;
}

:root {
	--brand: 38 87% 48%;
	--brand-light: 45 54% 71%;
	--dark: 0 0% 7%;
	--darker: 0 0% 9%;
	--dark-highlight: 0 0% 12%;
	--card: 0 0% 12%;
	--card-dark: 0 0% 8%;
	--card-dark-title: 0 0% 70%;
	--card-dark-border-inner: 0 0% 11%;
	--card-dark-border-outer: 0 0% 2%;
	--card-highlight: 0 0% 15%;
	--gray: 0 0% 20%;
	--gray-dark: 0 0% 16%;
	--gray-darker: 0 0% 14%;
	--gray-500: 216 6% 46%;
	--primary: 0 0% 93%;
	--secondary: 0 0% 82%;
	--tertiary: 0 0% 49%;
	--common: rgb(150, 150, 150);
	--uncommon: rgb(36, 108, 15);
	--rare: rgb(100, 175, 200);
	--heroic: rgb(134, 95, 8);
	--epic: rgb(80, 13, 172);
	--legendary: rgb(200, 115, 30);
	--artifact: rgb(177, 20, 20);
	--offblack: 0 0% 6%;
}